@import url(https://fonts.googleapis.com/css?family=Fira+Sans+Condensed:400,500|Indie+Flower|Oxygen:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/* general styling */
.App {
  text-align: center;
  font-family: 'Fira Sans Condensed', 'sans-serif';
  color: #030027;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

button:hover {
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.landingPage button:focus, .formSection input:focus, .formSection .entryText textarea:focus, .myEntries button:focus {
  border: 3px solid rgb(78, 160, 226);
}

/* landing page styling */

.landingPage {
  background:#CCDBDC;
}

.landingPage h1 {
  font-family: 'Indie Flower';
  margin: 0 auto;
  padding-top: 100px;
  font-size: 6rem;
}

.landingPage h2 {
  margin: 0px;
}

.landingPage h3 {
  margin-bottom: 80px;
}

.landingPage button {
  background: #36213E;
  color: #FDFFFC;
  border: none;
  padding: 20px 50px;
  font-size: 2rem;
  margin-bottom: 125px;
}

.landingPage button:hover {
  background: #030027;
  border-color: #FDFFFC;
}


.formSection form {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 0 auto;
}

.formSection input {
  text-align: left;
  width: 70%;
  padding: 10px;
  margin: 0 10px;
  border: none;
}

.formSection label {
  font-size: 1.5rem;
  text-align: left;
  width: 30%;
}

.formSection .input {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 15px;
  width: 90%;
  text-align: left;
}

/* .input input {
  padding: 10px;
  margin: 0 10px;
  border: none;
} */

.formSection h1 {
  padding: 30px 0;
  font-size: 4.3rem;
  color: #030027;
}

.formSection textarea {
  resize: none;
  margin: 0 10px;
  border: none;
  padding: 10px;
  width: 70%;
}

/* header styling */
.appHeader {
  background: #36213E;
}

.navMenu {
  display: flex;
  direction: row;
  justify-content: space-between;
  color: #FDFFFC;
}

.navMenu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0px;
}


.navMenu li {
  padding: 20px;
  font-size: 1.5rem;
}

.navMenu p {
  font-size: 1.5rem;
  padding-left: 20px;
}


.submitButton button {
  font-size: 1.5rem;
  margin: 30px 0px;
}

/* entries section styling */

.myEntries {
  background: #FDFFFC;
  color: #030027;
}

.myEntries h1 {
  font-family: 'Indie Flower';
  font-size: 4rem;
  margin: 0;
  padding-top: 30px;
}

.myEntries ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
}

.myEntries li {
  background: #c5c5c5;
  margin: 20px;
  width: calc(33% - 40px);
  color: #FDFFFC;
}

.myEntries button {
  width: 100%;
  background: rgba(197,197,197,0);
  padding: 0;
  position: relative;
  z-index: 99;
  border: 2px solid #c5c5c5;
}

.myEntries button:hover {
  border: 2px solid #030027;
  -webkit-transition: none;
  transition: none;
}

.myEntries h4, .myEntries p {
  position: relative;
  z-index: -1;
}

/* single entry page styling */

.singleEntry {
  background: #FDFFFC;
  border: 2px solid #030027;
  margin: 50px auto;
  width: 70%;
}

.singleEntry h5 {
  font-family: 'Indie Flower';
  font-size: 3rem;
  margin: 0;
  padding-top: 30px; 
  text-transform: uppercase;
  color: #030027;
}

.singleEntry h6 {
  font-size: 1.5rem;
  margin: 0;
  font-family: 'Fira Sans Condensed', 'sans-serif';
}

.singleEntry p {
  margin-top: 30px;
  padding: 0 40px;
  text-align: left;
  line-height: 1.8;
  font-size: 1.2rem;
}

/* footer styling */

.appFooter {
  background: #030027;
  color: #FDFFFC;
}

.appFooter p {
  margin: 0 auto;
  padding: 10px 0;
  text-align: center;
}

/* media queries */

@media (max-width: 700px) {
  .formSection h1 {
    font-size: 4rem;
  }

  .formSection .input {
    flex-direction: column;
    margin: 0 auto;
  }

  .formSection label, .formSection input {
    width: 100%;
    margin: 10px 0;
  }

  .formSection textarea {
    width: 100%;
    margin: 10px 0;
  }

  .myEntries li {
  width: calc(50% - 40px);
}
}

@media (max-width: 600px) {
  .landingPage h1 {
    font-size: 5rem;
  }

  .myEntries h1 {
    font-size: 3rem;
  }

  .singleEntry {
    width: 80%;
  }

  .entryContainer p {
    text-align: justify;
  }
}

@media (max-width: 480px) {
  .landingPage h1 {
    font-size: 3rem;
  }

  .landingPage h2, .landingPage h3{
    font-size: 1.1rem;
  }

  .landingPage button {
    padding: 15px 35px;
  }

  .formSection form {
    width: 100%;
  }

  .formSection .input, .formSection label {
    text-align: center;
  }

  .myEntries ul {
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .myEntries li {
    width: 90%;
  }

  .entryContainer h5 {
    font-size: 2.5rem;
  }

  .entryContainer h6 {
    font-size: 1.2rem;
  }

  .entryContainer p {
    font-size: 1.1rem;
    padding: 15px;
  }

  .singleEntry {
    width: 90%;
  }
}


